import ExcelJS from "exceljs";
import { saveAs } from "file-saver";

export async function exportSheet(test_id, test_name, quizs) {
    const headerColumnsDefault = [
        "學生編號",
        "學校",
        "年級",
        "姓名",
        "檢測日期",
        "得分",
    ];
    // 抓取得分的欄位索引
    const score_idx = headerColumnsDefault.findIndex((e) => e == "得分");
    const maxScore = quizs.length;

    let sheet_name = test_name + test_id;
    let workbook = new ExcelJS.Workbook();
    let sheet = workbook.addWorksheet(sheet_name);
    sheet.views = [{ state: "frozen", xSplit: score_idx + 1, ySplit: 3 }];
    sheet.addConditionalFormatting({
        ref: 'G4:BD53',
        rules: [
            {
                type: 'expression',
                formulae: ['INDIRECT(ADDRESS(3,COLUMN()))<>INDIRECT(ADDRESS(ROW(),COLUMN()))'],
                style: { fill: { type: 'pattern', pattern: 'solid', bgColor: { argb: 'FFFF8888' } } },
            },
            {
                type: 'expression',
                formulae: ['INDIRECT(ADDRESS(3,COLUMN()))=INDIRECT(ADDRESS(ROW(),COLUMN()))'],
                style: { fill: { type: 'pattern', pattern: 'solid', bgColor: { argb: 'FF99FF88' } } },
            }
        ]
    })
    const headerColumns = [
        ...headerColumnsDefault,
        ...quizs.map((quiz, idx) => `第${idx + 1}題`),
    ];
    const idRow = [
        ...new Array(headerColumnsDefault.length).fill(""),
        ...quizs.map((quiz) => quiz.boyo_id),
    ];
    const ansRow = [
        ...new Array(headerColumnsDefault.length).fill(""),
        ...quizs.map((quiz) => `${quiz.answer}`),
    ];
    const blankRow = [
        ...new Array(headerColumnsDefault.length - 2).fill(""),
        new Date().toLocaleDateString("zh"), // Date column, need to be current date
        "",
        ...new Array(quizs.length).fill(""),
    ];
    sheet.columns = [
        ...headerColumnsDefault.map((column) => ({
            width: column.length * 4,
        })),
        ...new Array(quizs.length).fill({ width: 10 }),
    ];
    sheet.addRows([
        headerColumns,
        idRow,
        ansRow,
        ...new Array(50).fill(blankRow),
    ]);
    sheet.mergeCells("A1:A3");
    sheet.mergeCells("B1:B3");
    sheet.mergeCells("C1:C3");
    sheet.mergeCells("D1:D3");
    sheet.mergeCells("E1:E3");
    sheet.mergeCells("F1:F3");
    sheet.eachRow((row,r_idx) => {
        row.eachCell((e,c_idx) => {
            e.style = {
                font: { name: "微軟正黑體" },
            };
            e.alignment = {
                vertical: "middle",
                horizontal: "center",
                wrapText: true,
            };
            if(c_idx > score_idx && r_idx > 3) {
                e.dataValidation = {
                    type: "list",
                    allowBlank: true,
                    formulae: ['"A,B,C,D,無"'],
                    showErrorMessage: true,
                    errorStyle: "error",
                    errorTitle: "錯誤",
                    error: `答案必須是ABCD`,
                };
            }
        });
    });
    sheet.columns[score_idx].eachCell((cell, row_num) => {
        if (row_num < 4) return;
        cell.dataValidation = {
            type: "whole",
            operator: "between",
            allowBlank: true,
            formulae: [0, maxScore],
            showErrorMessage: true,
            errorStyle: "error",
            errorTitle: "錯誤",
            error: `得分必須是介於0~${maxScore}之間的整數`,
        };
    });
    sheet.columns[score_idx-1].eachCell((cell, row_num) => {
        if (row_num < 4) return;
        cell.dataValidation = {
            type: "date",
            operator: "lessThan",
            allowBlank: true,
            formulae: [new Date(new Date().getTime() + 86400000)],
            showErrorMessage: true,
            errorStyle: "error",
            errorTitle: "錯誤",
            error: `日期不能設在未來`,
        };
        cell.numFmt = "yyyy/mm/dd";
    });

    let buffer = await workbook.xlsx.writeBuffer();
    let file_blob = new Blob([buffer]);
    saveAs(file_blob, `${test_name}-${test_id}登記表.xlsx`);
}
