import { ref } from "vue";

export const useMenu = () => {
    const toggleSideBar = () => {
        toggleSideBarR();
    }
    return { SideBarItems, SideBarState, toggleSideBar };
};

const toggleSideBarR = () => {
    SideBarState.value ^= true;
};

const SideBarState = ref(false);

const SideBarItems = ref([
    { title: "檢測卷產生工具", icon: "quiz", to: "Test" },
])