// import Admin from './views/Admin.vue';
import Test from "./views/Test.vue";
import Error from "./views/Error";
import MathPaper from "./views/MathPaper.vue";
// const { UserStatus } = useUserStatus();

const routes = [
    { name: "Test", path: '/', component: Test, meta: { permission: "LoggedIn", title: "首頁" } },
    { name: "MathPaper", path: '/MathPaper', component: MathPaper, meta: { permission: "LoggedIn", title: "首頁" } },
    // { name: "Home", path: '/home', component: Admin, meta: { permission: "any", title: "錯誤頁面" }, props: true },
    { name: "error", path: '/error', component: Error, meta: { permission: "any", title: "錯誤頁面" }, props: true },
]

export const router = createRouter({
    history: createWebHashHistory(),
    routes
})

