<template>
  <va-card>
    <div class="row">
      <div class="flex md8 lg8 offset--lg2" style="padding: 2">
        <h1>數學檢測卷測試</h1>
        <textarea v-model="OriginalText" />
        <va-button @click="downloadPaper" class="btn-primary">下載檢測卷</va-button>
      </div>
    </div>
  </va-card>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import { ref } from "vue";
import { useProducePaper } from "../compositions/useMathPaper.js";
// import Tiptap from '../components/Tiptap.vue'

const { initDoc, setTitle, savePaper ,addMathParagraph} = useProducePaper();

const OriginalText = ref('');

const downloadPaper = async () => {
  initDoc();
  await setTitle(`數學檢測測試`);
  await addMathParagraph(OriginalText.value,24);
  savePaper(`數學檢測測試.docx`);
  console.log(OriginalText);
};

</script>

<style>
.field {
  margin: 0.5rem;
  align-items: center;
}
</style>
