const VBs = require("../../const/BoyoVBs.json");
import BoyoVBLevels from "../../const/BoyoVBLevel";

import { Random } from "random-js";
let random = new Random();

function shuffleArray(array) {
    for (let i = array.length - 1; i > 0; i--) {
        const j = random.integer(0, i);
        [array[i], array[j]] = [array[j], array[i]];
    }
}

let BoyoVBs = VBs.map(vb => ({ ...vb }));

export const makeQuizs = (lesson = false, type = "C2E", size = 100) => {
    let quizVBsMajor = [];
    let quizVBsMinor = [];
    let quizVBs = [];
    if (lesson !== false) {
        let BoyoVBLevel = BoyoVBLevels.find(e => e.level === lesson);
        if (typeof BoyoVBLevel === "undefined") {
            throw new Error("沒有這個檢測單元:" + lesson);
        } else {
            let VBLevelUSE = BoyoVBLevel ? BoyoVBLevel : { VBRange: [0, 1206] };
            quizVBsMajor = BoyoVBs.filter((e) => e.id <= VBLevelUSE.VBRange[1] && e.id >= VBLevelUSE.VBRange[0]);
            if (VBLevelUSE.VBRange[0] >= 30) {
                quizVBsMinor = BoyoVBs.filter((e) => e.id <= VBLevelUSE.VBRange[0]);
                shuffleArray(quizVBsMinor);
                shuffleArray(quizVBsMajor);
                quizVBs = [
                    ...quizVBsMajor.slice(0, Math.floor(size * 0.7)),
                    ...quizVBsMinor.slice(0, Math.floor(size * 0.3))
                ]
            } else {
                shuffleArray(quizVBsMajor);
                quizVBs = quizVBsMajor;
            }
        }
    } else {
        quizVBs = [...BoyoVBs];
        shuffleArray(quizVBs);
    }
    quizVBs = quizVBs.slice(0, size);

    let result = quizVBs.map((e, idx) => {
        let used_idxs = [idx];
        let ans_idx = random.integer(0, 3);
        let options = [];
        for (let i = 0; i < 4; i++) {
            if (i === ans_idx) {
                options.push(e);
                continue;
            }
            let option_vb_idx;
            do {
                option_vb_idx = random.integer(0, quizVBs.length - 1);
            } while (
                used_idxs.includes(option_vb_idx) ||
                quizVBs[option_vb_idx].exp.includes(e.exp) ||
                e.exp.includes(quizVBs[option_vb_idx].exp)
            );
            used_idxs.push(option_vb_idx);
            options.push(quizVBs[option_vb_idx]);
        }

        if (type === "C2E") {
            return {
                boyo_id:
                    e.boyo_id,
                quiz: e.exp,
                option1: options[0].vb,
                option2: options[1].vb,
                option3: options[2].vb,
                option4: options[3].vb,
                answer: String.fromCharCode(ans_idx + 65),
            };
        } else {
            return {
                boyo_id:
                    e.boyo_id,
                quiz: e.vb,
                option1: options[0].exp,
                option2: options[1].exp,
                option3: options[2].exp,
                option4: options[3].exp,
                answer: String.fromCharCode(ans_idx + 65),
            };
        }
    });
    return result;
};

export default { makeQuizs };