<template>
  <va-card>
    <div class="row">
      <div class="flex md8 lg8 offset--lg2" style="padding: 2">
        <h1>博幼單字認讀檢測卷</h1>
        <div class="field">
          <va-select maxHeight="320px" class="mb-4" label="選擇檢測範圍" v-model="value" :options="selectionOptions" />
        </div>
        <div class="field">
          <va-button color="primary" gradient @click="paperGenerate">產生檢測卷</va-button>
          <va-button gradient outline color="primary" @click="openURLNew('https://youtu.be/JEbGvF0aqZ0')">使用教學
          </va-button>
        </div>
        <div class="field">
          <va-button gradient color="secondary" @click="openURLNew('https://ams.boyo.org.tw/boyost/boyoscoremanage')">
            線上登記成績</va-button>
          <va-button gradient outline color="secondary" @click="openURLNew('https://youtu.be/ebrl9HWFLYY')">線上登記教學
          </va-button>
        </div>
        <div class="field">
          <va-button gradient color="success" @click="downloadPaper(false)" :disabled="Paper === null">下載檢測卷</va-button>
          <va-button gradient color="success" @click="downloadPaper" :disabled="Paper === null">下載解答卷</va-button>
          <va-button gradient color="success" outline @click="openURLNew('https://youtu.be/OoECKVmvX9A')">檢測卷說明
          </va-button>
        </div>
        <div class="field">
          <va-button gradient color="secondary" @click="downloadSheet" :disabled="Paper === null">下載登記表</va-button>
          <va-button gradient color="secondary" outline @click="openURLNew('https://youtu.be/8TnhYbpe3dk')">登記表說明
          </va-button>
        </div>
        <div class="field" v-if="Paper !== null && Paper.original_paper !== undefined">
          <table class="va-table va-table--striped va-table--hoverable">
            <thead>
              <tr>
                <th>題號</th>
                <th>單字</th>
                <th>解答</th>
                <th>A</th>
                <th>B</th>
                <th>C</th>
                <th>D</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(quiz, idx) in Paper.original_paper" :key="'quiz:' + idx">
                <td>{{ idx + 1 }}</td>
                <td>{{ quiz.quiz }}</td>
                <td>{{ quiz.answer }}</td>
                <td>{{ quiz.option1 }}</td>
                <td>{{ quiz.option2 }}</td>
                <td>{{ quiz.option3 }}</td>
                <td>{{ quiz.option4 }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="field" v-else>
          <h3>檢測卷尚未產生</h3>
        </div>
      </div>
    </div>
  </va-card>
</template>

<script setup>
/* eslint-disable no-unused-vars */
import { ref } from "vue";
import BoyoVBLevel from "../const/BoyoVBLevel";
import { Generator } from "../lib/VB/Generator";
import { useProducePaper } from "../compositions/useProducePaper";
import { exportSheet } from "../compositions/useExcel";
import { v4 as uuidv4 } from "uuid";
import { openURLNew } from "../lib/Utils"


const { initDoc, addQuiz, setTitle, savePaper, addMathParagraph } = useProducePaper();

const selectionOptions = ref(
  BoyoVBLevel.map((level) => ({
    text: level.name,
    value: level.level,
  }))
);
const value = ref(selectionOptions.value[0]);
const Paper = ref(null);
const dlChecker = ref({
  paperDownloaded: true,
  sheetDownloaded: true,
});
const paperGenerate = async () => {
  if (dlChecker.value.paperDownloaded === false || dlChecker.value.sheetDownloaded === false) {
    if (!window.confirm("產生的試卷或登記表尚未下載，確定要重新產生試卷？")) {
      return;
    }
  }
  dlChecker.value.paperDownloaded = false;
  dlChecker.value.sheetDownloaded = false;
  let test_lv = value.value.value;
  let size = 50;
  if (test_lv < 3) {
    size = 30;
  } else if (test_lv < 5) {
    size = 40;
  }
  let test_paper_name = value.value.text;
  let original_paper = Generator("E2C", value.value.value, size);
  let uuid = uuidv4();
  Paper.value = {
    uuid,
    test_paper_name,
    original_paper,
  };
};

const downloadPaper = async (withAns = true) => {
  const { uuid, test_paper_name, original_paper } = Paper.value;
  initDoc();
  await setTitle(`${test_paper_name}`);
  original_paper.forEach((item, idx) => {
    addQuiz({
      ...item,
      quiz_num: idx + 1,
    }, withAns);
  });
  savePaper(`${test_paper_name}-${uuid}.docx`);
  dlChecker.value.paperDownloaded = true;
};

const downloadSheet = async () => {
  const { uuid, test_paper_name, original_paper } = Paper.value;
  exportSheet(uuid, test_paper_name, original_paper);
  dlChecker.value.sheetDownloaded = true;
};
</script>

<style>
.field {
  margin: 0.5rem;
  align-items: center;
}
</style>
