async function convertTeXImage(tex, fontSize = 12) {
    const svgString = loadTeX(tex);
    const svgPNG = await exportSvg(svgString, "png", fontSize / 4);
    const { w, h } = await getImageDimensions(svgPNG);
    const Image = new ImageRun({
        data: svgPNG,
        transformation: {
            width: w / 4,
            height: h / 4,
        },
    });
    return Image;
}

async function addMathParagraph(originalString = "", fontSize = 12) {
    const TeXs = originalString.match(/\$\$([\S\s]*?)\$\$/g);
    const Texts = originalString.replaceAll((/\$\$([\S\s]*?)\$\$/g), "{splitter}").split("{splitter}");
    var pChildren = []
    for (let t_idx in Texts) {
        const theTextRun = new TextRun({
            text: Texts[t_idx],
            size: fontSize * 2
        });
        pChildren.push(theTextRun);
        if (TeXs!==null && t_idx < TeXs.length) {
            const theImage = await convertTeXImage(TeXs[t_idx].slice(2, TeXs[t_idx].length - 2), fontSize);
            pChildren.push(theImage);
        }
    }
    const mParagraph = new Paragraph({
        children: pChildren,
        alignment: AlignmentType.CENTER,
    });
    document.value.sections[0].children.push(
        mParagraph
    );
}

function getImageDimensions(file) {
    return new Promise(function (resolved) {
        var i = new Image()
        i.onload = function () {
            resolved({ w: i.width, h: i.height })
        };
        i.src = file
    })
}

const base_position = 1200;

const ConverterPtoTWIP = (font_size) => {
    return Math.floor(font_size * 20);
}

const CalculateTAB = (string_length, font_size) => {
    let result = Math.floor(ConverterPtoTWIP(font_size) * string_length);
    result = result < base_position * 3 ? base_position * 3 : result;
    return result;
}

