async function setTitleR(title) {
    let LOGOimages = await LoadLOGOImages();
    document.value.title = title;
    let title_p = new Paragraph({
        heading: HeadingLevel.TITLE,
        text: title,
        alignment: AlignmentType.CENTER
    });
    let name_p = new Paragraph({
        style: "name_paragraph",
        text: "姓名：\t\t\t\t",
        alignment: AlignmentType.RIGHT
    });
    document.value.sections[0].headers = {
        default: new Header({
            children: [
                new Paragraph({
                    children: [
                        ...LOGOimages,
                    ],
                }),
            ]
        })
    };
    document.value.sections[0].footers = {
        default: new Footer({
            children: [
                new Paragraph({
                    indent: {
                        right: convertMillimetersToTwip(30),
                    },
                    alignment: AlignmentType.RIGHT,
                    children: [
                        new TextRun(title),
                        new TextRun({
                            children: [
                                "第",
                                PageNumber.CURRENT,
                                "/",
                                PageNumber.TOTAL_PAGES,
                                "頁"],
                        }),
                    ],
                }),
            ],
        }),
    }
    if (document.value.sections[0].children.length === 0) {
        document.value.sections[0].children.push(
            title_p
        );
        document.value.sections[0].children.push(
            name_p
        );
    } else {
        document.value.sections[0].children[0] =
            title_p;
        document.value.sections[0].children[1] =
            name_p;
    }
}

function addQuiz(quiz_source) {
    if (document.value === null) {
        throw new Error("Document is not ready");
    }
    const { quiz, option1, option2, option3, option4, answer, quiz_num } = quiz_source;
    const quiz_p = new Paragraph({
        style: "quizcell_eng",
        indent: {
            hanging: base_position,
            start: base_position
        },
        children: [
            new TextRun(`${quiz_num}( `),
            new TextRun({
                text: `${answer}`,
                color: "#FFFFFF"
            }),
            new TextRun(` ).\t`),
            new TextRun(quiz),
            new TextRun({ break: 1 }),
            new TextRun({ text: "(A) ", bold: true, }),
            new TextRun({ text: option1, font: "標楷體" }),
            new TextRun({ text: "\t(B) ", bold: true, }),
            new TextRun({ text: option2, font: "標楷體" }),
            new TextRun({ break: 1 }),
            new TextRun({ text: "(C) ", bold: true, }),
            new TextRun({ text: option3, font: "標楷體" }),
            new TextRun({ text: "\t(D) ", bold: true, }),
            new TextRun({ text: option4, font: "標楷體" }),
        ],
        tabStops: [
            {
                type: TabStopType.LEFT,
                position: base_position + CalculateTAB(
                    Math.max(option1.length, option3.length)
                    , 10),
            },
        ],
    })
    document.value.sections[0].children.push(quiz_p);
}


async function savePaperR(fileName) {
    if (document.value === null) {
        throw new Error("Document is not ready");
    }
    const doc = new Document(document.value);
    try {
        let blob = await Packer.toBlob(doc)
        // saveAs from FileSaver will download the file
        saveAs(blob, fileName);
    } catch (e) {
        DealingErrorRedirect(e);
    }
}

