<template>
    <va-sidebar
        color="primary"
        gradient
        v-model="SideBarState"
        minimizedWidth="48px"
        width="12rem"
        style="position:absolute;"
    >
        <va-sidebar-item>
            <va-sidebar-item-content>
                <va-sidebar-item-title>test</va-sidebar-item-title>
            </va-sidebar-item-content>
        </va-sidebar-item>
        <va-sidebar-item
            v-for="item in SideBarItems"
            :key="item.to"
            :active="router.currentRoute.value.name === item.to"
        >
            <va-sidebar-item-content @click="go(item.to)">
                <va-icon :name="item.icon" />
                <va-sidebar-item-title style="height: 24px">
                    {{
                        item.title
                    }}
                </va-sidebar-item-title>
            </va-sidebar-item-content>
        </va-sidebar-item>
    </va-sidebar>
</template>

<script setup>
import { useMenu } from "../compositions/useMenu";
import { router } from "../router";
import { DealingErrorRedirect } from "../lib/Utils";

console.log(router);

const { SideBarState, SideBarItems, toggleSideBar } = useMenu();
const go = (to) => {
    try {
        toggleSideBar();
        router.push({
            name: to
        });
    } catch (err) {
        DealingErrorRedirect(err);
    }
}

</script>

